import React from 'react';
import PropTypes from 'prop-types';
import withStyles from '@material-ui/core/styles/withStyles';
import Card from '@material-ui/core/Card';
// import Paper from 'material-ui/Paper';
import Grid from '@material-ui/core/Grid';
import Input from '@material-ui/core/Input';
import FormControl from '@material-ui/core/FormControl';

import Email from '@material-ui/icons/Email';
import Phone from '@material-ui/icons/Phone';

import { useTranslation } from 'react-i18next';
import { CardMedia, InputLabel, Fab } from '@material-ui/core';

const styles = theme => ({
  root: {
    boxSizing: 'border-box',
    width: '100%',
    padding: theme.spacing.unit * 3
  },
  card: {
    display: 'flex',
    width: 'auto',
    maxWidth: 900,
    margin: '0 auto'
  },
  avatar: {
    margin: theme.spacing.unit * 3,
    width: 192,
    height: 192,
    backgroundColor: theme.palette.primary
  },
  flex1: {
    display: 'flex',
    flex: 1
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
    flex: 1
  },
  content: {
    flex: '1 0 auto',
    display: 'flex',
    flexDirection: 'row',
    padding: theme.spacing.unit * 3,
    justifyContent: 'space-around'
  },
  media: {
    width: 225
  },
  formDetails: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between'
  },
  icon: {
    margin: theme.spacing.unit,
    padding: theme.spacing.unit
  }
});
const PersonalData = ({ classes }) => {
  const { t } = useTranslation('pdata');
  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        {<CardMedia image="images/me.jpg" className={classes.media} />}
        <div className={classes.details}>
          <div className={classes.content}>
            <Grid container spacing={24}>
              <Grid item xs={12} md={6} className={classes.formDetails}>
                <FormControl margin="normal">
                  <InputLabel htmlFor="lbl_full_name">{t('controls:lbl_name')}</InputLabel>
                  <Input id="lbl_full_name" value={t('values:name')} />
                </FormControl>
                <FormControl margin="normal">
                  <InputLabel htmlFor="lbl_birth_date">{t('controls:lbl_birth_date')}</InputLabel>
                  <Input id="lbl_birth_date" value={t('values:birth_date')} />
                </FormControl>
                <FormControl margin="normal">
                  <InputLabel htmlFor="lbl_country">{t('controls:lbl_location')}</InputLabel>
                  <Input id="lbl_country" value={t('values:location')} />
                </FormControl>

                <div className={classes.flex1}>
                  <FormControl margin="normal">
                    <InputLabel>{t('controls:lbl_mail')}</InputLabel>
                    <Input value="gallayb@gmail.com" />
                  </FormControl>
                  <Fab
                    color="primary"
                    onClick={() =>
                      (window.location.href = `mailto:gallayb@gmail.com?subject=${t(
                        'common:Job opportunity'
                      )}`)
                    }>
                    <Email />
                  </Fab>
                </div>
              </Grid>
              <Grid item xs={12} md={6} className={classes.formDetails}>
                <FormControl margin="normal">
                  <InputLabel htmlFor="lbl_birth_place">{t('controls:lbl_birth_place')}</InputLabel>
                  <Input id="lbl_birth_place" value={t('values:birth_place')} />
                </FormControl>
                <div className={classes.flex1}>
                  <FormControl margin="normal">
                    <InputLabel>{t('controls:lbl_phone_number')}</InputLabel>
                    <Input value={t('values:phone_number')} />
                  </FormControl>
                  <Fab
                    color="primary"
                    onClick={() => (window.location.href = `tel:${t('+36202343963')}`)}>
                    <Phone />
                  </Fab>
                </div>
              </Grid>
            </Grid>
          </div>
        </div>
      </Card>
    </div>
  );
};

PersonalData.propTypes = { classes: PropTypes.object.isRequired };

export default withStyles(styles)(PersonalData);
