import React from 'react';
import { useTranslation } from 'react-i18next';
import Button from '@material-ui/core/Button';

const TranslateButtons = () => {
  const { t, i18n } = useTranslation('controls');
  return (
    <div>
      <Button
        dense="true"
        raised={i18n.language === 'hu-HU' ? 'true' : 'false'}
        color={i18n.language === 'hu-HU' ? 'primary' : 'default'}
        onClick={() => i18n.changeLanguage('hu-HU')}>
        {t('btn_hungarian')}
      </Button>
      <Button
        dense="true"
        raised={i18n.language === 'en' ? 'true' : 'false'}
        color={i18n.language === 'en' ? 'primary' : 'default'}
        onClick={() => i18n.changeLanguage('en')}>
        {t('btn_english')}
      </Button>
    </div>
  );
};
export default TranslateButtons;
