import React, { Suspense } from 'react';

import findIndex from 'lodash/findIndex';
import { createMuiTheme, withStyles, MuiThemeProvider } from '@material-ui/core/styles';
import { Trans, Translation } from 'react-i18next';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import IconButton from '@material-ui/core/IconButton';
import TranslateButtons from './translate-buttons';
import TabButton from './tab-button';

// import BackgroundImage from '../../images/bg1.jpg';

const theme = outerTheme =>
  createMuiTheme({
    ...outerTheme,
    palette: Object.assign(
      {},
      {
        primary: outerTheme.palette.primary,
        secondary: outerTheme.palette.secondary,
        error: outerTheme.palette.error,
        type: 'dark'
      }
    )
  });

const styles = () => {
  return {
    selectedTab: {
      color: 'white',
      background: 'rgba(255,255,255,0.2)'
    },
    backgrounded: {
      backgroundImage: 'url(images/bg1.jpg)',
      backgroundPosition: 'center',
      backgroundSize: 'cover'
    },
    overlay: {
      background: 'rgba(0,0,0,0.5)'
    },
    whiteText: {
      color: 'white'
    },
    tabs: {
      width: '100%'
    }
  };
};

const Header = props => {
  const {
    classes,
    match: {
      params: { page = '' }
    }
  } = props;

  const handleChange = (event, value) => {
    props.history.push(pages[value].link);
  };

  const pages = [
    {
      link: '/',
      label: 'btn_personal_data'
    },
    {
      link: '/educations',
      label: 'btn_education'
    },
    {
      link: '/experiences',
      label: 'btn_experience'
    },
    {
      link: '/skills',
      label: 'btn_skills'
    },
    {
      link: '/contact',
      label: 'btn_contact'
    }
  ];

  const value = findIndex(pages, x => x.link === `/${page}`);

  return (
    <MuiThemeProvider theme={theme}>
      <AppBar className={classes.backgrounded}>
        <div className={classes.overlay}>
          <Toolbar>
            <div style={{ display: 'flex', flex: 1, flexDirection: 'column', padding: '15px' }}>
              <div style={{ display: 'flex', alignItems: 'center', flex: 1 }}>
                <Typography component="h1" variant="h5" className={classes.whiteText}>
                  <Suspense fallback="name">
                    <Translation>{t => t('values:name')}</Translation>
                  </Suspense>
                </Typography>
              </div>
              <Typography component="h3" className={classes.whiteText}>
                <Suspense fallback="position">
                  <Translation>{t => t('values:position') }</Translation>
                </Suspense>
              </Typography>
            </div>

            <div
              style={{
                display: 'flex',
                flex: 1,
                flexDirection: 'row',
                justifyContent: 'flex-end',
                alignItems: 'center'
              }}>
              <IconButton onClick={() => window.open('https://facebook.com/gallayb')}>
                <i className="fa fa-facebook-square" />
              </IconButton>
              <IconButton
                onClick={() => window.open('https://linkedin.com/in/balázs-gallay-b9b1b834')}>
                <i className="fa fa-linkedin-square" />
              </IconButton>
              <IconButton onClick={() => window.open('https://github.com/carathorys')}>
                <i className="fa fa-github-square" />
              </IconButton>
              <Suspense fallback="HUNGARIAN ENGLISH">
                <TranslateButtons />
              </Suspense>
            </div>
          </Toolbar>
          <Toolbar style={{ minHeight: 48 }}>
            <Tabs
              value={value}
              onChange={handleChange}
              indicatorColor="primary"
              variant="scrollable"
              className={classes.tabs}>
              {pages.map((page, index) => (
                <Tab
                  key={page.link}
                  label={
                    <Suspense fallback="">
                      <TabButton i18nKey={page.label} />
                    </Suspense>
                  }
                  className={value === index ? classes.selectedTab : ''}
                />
              ))}
            </Tabs>
          </Toolbar>
        </div>
      </AppBar>
    </MuiThemeProvider>
  );
};

export default withStyles(styles)(Header);
